import React from 'react'

export default function Button(props) {
    const {currentNumber, quizData, handleFinishQuiz,handleNextItem,disabled} = props

    const nextButton = <button className="action-button" onClick={handleNextItem} disabled={disabled}>Next Question</button>
    const finishButton = <button className="action-button" onClick={handleFinishQuiz} disabled={disabled}>Finish</button>
    //  if the item is not finished yet, next button will show
        //  if the quiz is finished, finish button will appear
    
    const buttons = currentNumber+1 < quizData.length ? nextButton : finishButton
        
    return (
        <div>
            {buttons}
        </div>
    )
}



