import React from 'react'

export default function Answers(props) {
    const {answer, userAnswer,handleChoose   } = props

    const answers = answer.map((item,index)=>{
        const hasChoice = userAnswer === item 
        return (
            <button 
                className={`choice-buttons${hasChoice? "-active": ""}`}
                key={index}
                onClick={handleChoose}
            >
                {item}
            </button>
        )
        
    })
    return (
        <span>
            {answers}
        </span>
    )
}



