/* eslint-disable */

import React, { Component } from 'react'
import quizData from "./data"
import Questions from "./components/Questions"
import Answers from "./components/Answers"
import Button from "./components/Button"
import FinishQuiz from "./components/FinishQuiz"
import "./app.css"

export default class App extends Component {
    state = {
        currentNumber:0,
        userAnswer:"",
        isFinished: false,
        disabled: true,
        score:0,
        question:[],
        answer:[],
        correct:"",
        answerArray:[],             
        correctArray: [] 
    }
    componentDidMount(){
        const {currentNumber} = this.state
        this.setState({
                question: quizData[currentNumber].question,
                answer: quizData[currentNumber].answers,
                correct: quizData[currentNumber].correct,
            })
    }
    // this is for automatically setting state for components 
    componentDidUpdate(prevProps,prevState){
        const {currentNumber} = this.state
        if(prevState.currentNumber!==currentNumber){
            this.setState({
                question    : quizData[currentNumber].question,
                answer      : quizData[currentNumber].answers,
                correct     : quizData[currentNumber].correct,
                disabled    : true
            })
        }
    }
    // next item is taken care of 
    handleNextItem = ()=>{
        const {currentNumber,correct,score,userAnswer} = this.state 
        this.setState({
            currentNumber : currentNumber + 1,
            // if correct , score + 1
            score: userAnswer === correct ? score + 1 : score,
            answerArray: [...this.state.answerArray , this.state.userAnswer],
            correctArray: [...this.state.correctArray, this.state.correct]
        })
    }
    // for choosing options on the questions given
    handleChoose = (e)=>{
        this.setState({
            userAnswer  : e.target.innerHTML,

            // this will trigger the disabled property on the button. You just have to choose any
            // option on the screen
            disabled    :false
        })
    }
    // for handling scores as soon as the quiz ended
    handleFinishQuiz = ()=>{
        const {userAnswer, correct, score} = this.state
        this.setState({
            isFinished  : true,
            score       : userAnswer === correct ? score + 1 : score,
            answerArray : [...this.state.answerArray, this.state.userAnswer],
            correctArray: [...this.state.correctArray, this.state.correct]            
        })
    }
    // =-========-========-========-========-========-========-========-========-========-========-========-========-========-=======
    render() {
        // deconstructing this motherfuckers so my code would look clean
        const {currentNumber, question, answer,isFinished,score,userAnswer,disabled,correct} = this.state
        
        // handleButton functions
        const {handleChoose , handleFinishQuiz , handleNextItem} = this

        
        // if the quiz is at the end of the quiz and you clicked finish
        if(isFinished){
            return(
                <div className="main-container">
                <FinishQuiz
                    score       ={score}
                    quizData    ={quizData}
                    userAnswer  ={userAnswer}
                    question    ={question}
                    correctArray={this.state.correctArray}
                    answerArray ={this.state.answerArray}
                />
                </div>
            )
        }
            return (
                <div className="main-container" >
                    <h1 id="quiz-title">Quiz</h1>
                    <span>{currentNumber + 1} of {quizData.length}</span>
                    <Questions 
                        currentNumber   ={currentNumber}
                        question        ={question}
                        quizData        ={quizData}
                    />
                    <Answers 
                        display         ={this.state.display}
                        answer          ={answer}
                        userAnswer      ={userAnswer}
                        handleChoose    ={handleChoose}
                        correct         ={correct}
                    />
                    <Button 
                        currentNumber   ={currentNumber}
                        quizData        ={quizData}
                        disabled        ={disabled}
                        handleNextItem  ={handleNextItem}
                        handleFinishQuiz={handleFinishQuiz}
                    />
                </div>
        )
    }
}



