import React from 'react'

export default function FinishQuiz(props) {
    const {quizData, score , correctArray , answerArray} = props
    
    return (
        <div>
            <div>
                <h1 id="game-over">Game Over</h1>
                <h3 id="score">Your Score is {score} out of {quizData.length}</h3>
                
                {quizData.map((item,index)=>{
                    return (
                        <div>
                            <p>{index+1}.{item.question}</p>
                            <p><strong>Correct Answer: </strong>{correctArray[index]} </p>
                            <p><strong>Your Answer : </strong>{answerArray[index]}{correctArray[index]===answerArray[index]? <div className="correct">correct</div>: <div className="wrong"> wrong</div>}</p>
                        </div>
                        
                    )
                })}
            </div>
        </div>
    )
}



